.App {
    text-align: center;
}

.App-header{
    background-color : cornflowerblue;
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 14vh;
}

.Team-header{
    background-color : cornflowerblue;
    display: flex;
    align-items: center;
    height: 8vh;
}
.App-main{
    background-color :  #045c5a;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Team-main{
    background-color :  #045c5a;
    min-height: 90vh;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly
}

.tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    margin-top: 0vh;
    margin-bottom: 5vh;
    padding: 4px;
    text-align: center;
    color: #045c5a;
    font-size: 50px;
}

.tab-cell{
    border : 1px solid;
    border: collape;
    border-color: black;
}
.tab-body{
    color:#045c5a;
    text-justify: center;
    font-size:25px;
}

.tab-logo {
    border: 1px solid;
    border-color: black;
    border: collapse;
    justify-items: center
}

.teamLogo{
   width : 30px;
   height : 30px;
   border-radius: 100%;
}

.upcoming{
    flex-direction:column;
    align-items: center;
    justify-content: center;
    background-color: grey;
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    border-radius: 90px;
    margin: 1vh 2vw;
    width : 9rem;
    height: 3.5rem;
    font-size: 0.7rem;
}

.player-table-container {
    overflow-y: scroll;
    max-height : 45vh;
    border-radius: 10px;
    max-width : 50vw;
}

.player-tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    padding: 4px;
    margin-top: 0;
    text-align: center;
    color: #045c5a;
    font-size: 20px;
    overflow: scroll;
}

.team-tab{
    background-color : whitesmoke;
    border-radius: 10px;
    overflow: 'hidden';
    height : 40vh;
    width: 30vw;
    padding: 4px;
    margin-top: 0vh;
    text-align: center;
    color: #045c5a;
    font-size: 20px;
}

.team-img{
    width : 5vw;
    margin-left: 92vw;
}

.form-container {
    display: flex;
    flex-direction: row;
    
}
.stats-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 70vw;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 40px;
    color: white;
}

.matches-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 30vw;
}

.match-box {
    border-radius : 10px;
    min-width: 6vw;
    max-width: 8vw;
    max-height: 7vh;
    background-color: violet;
    color: white;
    font-size: 12px;
    margin-bottom: 5vh;
}

.home-button {
    border-radius : 10px;
    background-color: violet;
    color: black;
}